@import url(https://fonts.googleapis.com/css2?family=Space+Mono:wght@400;700&display=swap);
/** @format */
body {
	margin: 0;
	box-sizing: border-box;
	font-family: 'Space Mono', monospace;
}
code {
	font-family: 'Space Mono', monospace;
}

/** @format */
:root {
	--strong-cyan: hsl(172, 67%, 45%);
	--very-dark-cyan: hsl(183, 100%, 15%);
	--dark-grayish-cyan: hsl(184, 14%, 56%);
	--light-grayish-cyan-1: hsl(185, 41%, 84%);
	--light-grayish-cyan: hsl(189, 41%, 97%);
	--white: hsl(0, 0%, 100%);
}
html,
body {
	box-sizing: border-box;
	padding: 0;
	margin: 0;
	font-family: 'Space Mono', monospace;
}
.App {
	font-family: 'Space Mono', monospace;
	box-sizing: border-box;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;
	background-color: hsl(184, 14%, 56%);
	background-color: var(--dark-grayish-cyan);
	width: 100%;
	height: 100vh;
	overflow-y: scroll;
}
.calculator-container {
	background-color: hsl(0, 0%, 100%);
	background-color: var(--white);
	display: flex;
	align-items: center;
	justify-content: space-evenly;
	border: 1px solid rebeccapurple;
	border-radius: 20px;
	padding: 1rem;
}
@media only screen and (max-width: 600px) {
	.App {
		height: auto;
	}
	.calculator-container {
		flex-direction: column;
		align-content: center;
		justify-content: center;
		margin-bottom: 2rem;
	}
	.logo {
		margin: 2rem 0 5rem 0;
	}
}

/** @format */
.bill-calculator-container {
	box-sizing: border-box;
	font-size: 1rem;
	min-width: 270px;
	max-width: 300px;
	margin-right: 2rem;
}

.bill-input {
	display: flex;
	justify-content: space-between;
	align-items: center;
	background-color: var(--light-grayish-cyan);
}
.input-icon {
	padding-left: 1rem;
}
.bill-input input {
	font-size: 24px;
	color: var(--very-dark-cyan);
	font-weight: 700;
	font-family: 'Space Mono', monospace;
	background: none;
	outline: none;
	text-align: right;
	border: none;
	width: 90%;
}

.tip-options {
	display: flex;
	justify-content: flex-start;
	flex-wrap: wrap;
	grid-gap: 5px;
	gap: 5px;
}

.person-input {
	display: flex;
	justify-content: space-between;
	align-items: center;
	background-color: var(--light-grayish-cyan);
}
.person-input input {
	color: var(--very-dark-cyan);

	font-weight: 700;
	background: none;
	font-size: 24px;
	font-family: 'Space Mono', monospace;

	outline: none;
	text-align: right;
	border: none;
	width: 100%;
}
/* TIP PERCENTAGE COMPNENT */
.percentage-btn {
	font-weight: 700;
	font-size: 1rem;
	min-width: 95px;
	padding: 0.5rem 1rem;
	border: none;
	border-radius: 3px;
	color: var(--white);
	background-color: var(--very-dark-cyan);
	cursor: pointer;
	font-family: 'Space Mono', monospace;
}
.custom {
	color: hsl(183, 100%, 15%);
	background-color: hsl(185, 41%, 84%);
	min-width: 95px;
}
.percentage-btn:hover {
	background-color: var(--strong-cyan);
}

@media only screen and (max-width: 600px) {
	.bill-calculator-container {
		margin-bottom: 2rem;
	}
	.percentage-btn,
	.custom {
		width: 49%;
	}
}
@media only screen and (max-width: 650px) {
	.bill-calculator-container {
		margin-right: auto;
	}
}

/** @format */

.bill-display-container {
	box-sizing: border-box;
	min-width: 270px;
	max-width: 300px;
	background-color: var(--very-dark-cyan);
	border-radius: 15px;
	padding: 1rem;
	font-size: 1rem;
}
.tip-amount {
	display: flex;
	align-items: center;
	justify-content: space-between;
}
.tip-amount-label,
.total {
	font-size: 0.6rem;
}
.heading {
	margin: 0;
	color: var(--light-grayish-cyan-1);
}
.sub-heading {
	color: var(--dark-grayish-cyan);
	margin: 0;
}
.total {
	display: flex;
	align-items: center;
	justify-content: space-between;
}
.label {
	font-size: 1.5rem;
	color: var(--strong-cyan);
	margin: 10px 0;
}
.reset-btn {
	width: 100%;
	background-color: var(--strong-cyan);
	border: none;
	padding: 1rem;
	font-weight: 700;
	cursor: pointer;
	color: var(--very-dark-cyan);
	border-radius: 5px;
	margin-top: 8rem;
}
@media only screen and (max-width: 800px) {
	.bill-display-container {
		width: 100%;
	}
	.label {
		align-self: flex-end;
	}
}

